import { API, ApiError, ServerError } from 'api';
import { useQueryParams } from 'hooks/useQueryParams';
import { ParsedQuery } from 'query-string';
import { useState } from 'react';
import toast from 'react-hot-toast';
import { IEntitiesList } from 'types/entities';

export const useEntities = () => {
  const [isLoading, setIsLoading] = useState(false);

  const [entities, setEntities] = useState<IEntitiesList>({
    items: [],
    total: 0,
  });

  const params = useQueryParams();

  const getEntities = async (
    page: number,
    limit: number,
    lockedParams?: ParsedQuery<string>
  ) => {
    setIsLoading(true);
    let entities;

    try {
      entities = await API.entities.getList(
        page,
        limit,
        lockedParams || (params as any)
      );

      setIsLoading(false);
    } catch (e) {
      if (e instanceof ServerError) {
        toast('–ü—Ä–æ–∏–∑–æ—à–ª–∞ –∫—Ä–∏—Ç–∏—á–µ—Å–∫–∞—è –æ—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –ª—É—á—à–∏—Ö –ø—Ä–∞–∫—Ç–∏–∫!', {
          position: 'bottom-center',
        });
      } else if (e instanceof ApiError) {
        toast('–ù–µ —É–¥–∞–ª–æ—Å—å –ø–æ–ª—É—á–∏—Ç—å –ª—É—á—à–∏–µ –ø—Ä–∞–∫—Ç–∏–∫–∏', {
          position: 'bottom-center',
        });
      }
    }

    if (!entities?.data) {
      return;
    }

    setEntities(entities.data);
  };

  return { entities, isLoading, getEntities };
};
