import { API } from 'api';
import { ApiError, ServerError } from 'api/errors';
import toast from 'react-hot-toast';
import { useQuery } from 'react-query';
import { getMethodologyKey } from './../keys';

export const useMethodology = (id: string, isBest?: boolean) => {
  const query = useQuery(
    getMethodologyKey(id, isBest),
    () => API.methodology.get(id, isBest),
    {
      onError: (e) => {
        if (e instanceof ServerError) {
          toast(
            '–ü—Ä–æ–∏–∑–æ—à–ª–∞ –∫—Ä–∏—Ç–∏—á–µ—Å–∫–∞—è –æ—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –º–µ—Ç–æ–¥–∏–∫–∏ –∏ —Ç–µ—Ö–Ω–æ–ª–æ–≥–∏–∏!',
            {
              position: 'bottom-center',
            }
          );
        } else if (e instanceof ApiError) {
          toast(e.message, { position: 'bottom-center' });
        }
      },
    }
  );

  return {
    ...query,
    apiError: query.data?.error,
    apiData: query.data?.data ? query.data.data : null,
    isError: query.isError || (query.data?.error ? true : false),
  };
};
